import utils from './Utils.js';

const $i = element => document.getElementById(element);
const $q = (element) => document.querySelector(element);
const $a = (element) => document.querySelectorAll(element);

const $colors = {
    grey: '#696969',
    limegreen: '#bada55',
    lightblue: '#7fe5f0',
    brightred: '#ff0000',
    pink: '#ff80ed',
    seablue: '#407294',
    beige: '#cbcba9',
    white: '#ffffff',
    maroon: '#420420',
    grassgreen: '#065535',
    lightgrey: '#c0c0c0',
    yellow: '#ffd700',
    orange: '#ffa500',
    darkred: '#800000',
    limegreen: '#00ff00',

};

const $CRYPTO = {
    BTC: 'BTC',
    DIVI: 'DIVI',
    NANO: 'NANO',
    DOGE: 'DOGE',
    ETH: 'ETH',
    RIPPLE: 'RIPPLE',
};

const SATOSHI_BTC = 0.00000001;
const BTC_SATOSHI = 100_000_000;
const ETH_SATOSHI = 3_457_271.83;
const GBP_SATOSHI = 11_467.22
const DIVI_SATOSHI = 540.00;
const RIPPLE_SATOSHI = 2_794.53;
const DOGE_SATOSHI = 29.78;
const NANO_SATOSHI = 9_039.78;

// 1 Satoshi in Crypto
const $SATOSHI = {
    BTC: 0.00000001,
    ETH: 0.00000033,
    GBP: 0.00007498,
    DIVI: 0.00186219739,
    RIPPLE: 0.00038700,
    DOGE: 0.03333,
    NANO: 0.00009557,
}

const $GBP = {
    BTC: 0.00012,
    ETH: 0.0033,
    DIVI: 0.927533,
    RIPPLE: 4.432083,
    DOGE: 411.248727,
    NANO: 1.257049,
}

let keys = {
    $eventKey: {
        STATE_CHANGE: 'stateChange',
        CRYPTO_BOUGHT: 'cryptoBought',
        STASH_CHANGED: 'stashChanged',
        TRENCH_COAT_CHANGED: 'trenchCoatChanged',
        WALLET_CHANGED: 'walletChanged'
    },
    $actionKey: {},
    $mutationKey: {}
};

(() => {
    let keysToMap = [];
    Object.keys(keys).forEach($keyType => {
        Object
            .keys(keys[$keyType])
            .map(k => {
                if (keysToMap.includes({ [k]: keys[$keyType][k] })) return;
                return keysToMap.push(new Object({ [k]: keys[$keyType][k] }));
            });
    })

    keysToMap.forEach((el, i, arr) => {
        keys.$actionKey[Object.keys(el)[0]] = Object.values(el)[0];
        keys.$mutationKey[Object.keys(el)[0]] = Object.values(el)[0];
    });

    return void 0; 
})();

const $k = [];

(function gen(n) { 
    if (n > 0) {
        $k.push(utils.genRandomToken());
        return gen(--n);
    }

    return false
})(20);

const $x = (() => $k[utils.getRandomInt(0, Object.keys($k).length - 1)])();

utils.log('keys:', keys, $colors.orange);
utils.log('x-token:', $x, $colors.grassgreen);

export { $i, $q, $a, keys, $x, $CRYPTO, $GBP, $colors };