import Trader from './models/Trader.js';

export default class App {
    constructor({ store, components, $x, init }) {
        this.store = store;
        this.components = components;
        this.$x = $x;
        this.init = init;
        this.player = null;
        this.traders = [];
    }

    registerComponent(component, k) {
        this.components.push(component);
    }

    registerPlayer(player, k) {
        if (k === this.$x) return this.player = player;
        throw new Error('Unauthorised!');
    }

    generateTraders(traderData) {
        traderData.forEach(trader => {
            this.traders.push(new Trader({
                _id: trader['_id'], index: trader['index'], guid: trader['guid'], 
                isActive: trader['isActive'], balance: trader['balance'], age: trader['age'], 
                picture: trader['picture'], firstname: trader['name']['first'], 
                lastname: trader['name']['last'], company: trader['company'], email: trader['email'],
                company: trader['company'], phone: trader['phone'], registered: trader['registered'], 
                latitude: trader['latitude'], longitude: trader['longitude'], store: this.store
            }));
        });
    }

    sortTradersByBalance() {
        this.traders = this.traders.sort((traderA, traderB) => traderA.balance > traderB.balance ? -1 : 1)
    }
}