import Store from '../state/Store.js';
import { keys, $CRYPTO, $GBP } from '../lib/constants.js';

export class CryptoConverter {
    constructor() {}

    fiatToCrypto = (val, crypto) => val * $GBP[crypto];
    cryptoToFiat = (val, crypto) => val / $GBP[crypto];
}

export class Crypto {
    constructor(props) {
        if (props.hasOwnProperty('name') && props.hasOwnProperty('fiatVal') && props.store instanceof Store) {
            this.name = props.name;
            this.store = props.store;
            props.store.events.subscribe(keys.$eventKey.CRYPTO_BOUGHT, (props) => console.log(`${props.name} bought!`));
        } else {
            throw new TypeError('Invalid Crypto constructor props');
        }

        this.convert = new CryptoConverter();
        this.cryptoValue = this.convert.fiatToCrypto(props.fiatVal, this.name);
        this.fiatValue = this.convert.cryptoToFiat(this.cryptoValue, this.name);
    }

    convertTo(crypto, previousCrypto) {
        setTimeout(() => this.destroyCryptoInstance(previousCrypto), 0);
        switch (crypto) {
            case $CRYPTO.BTC:
                return new BTC(this.fiatValue, this.store);
            case $CRYPTO.DIVI:
                return new Divi(this.fiatValue, this.store);
            case $CRYPTO.ETH:
                return new Ethereum(this.fiatValue, this.store);
            case $CRYPTO.DOGE:
                return new Doge(this.fiatValue, this.store);
            case $CRYPTO.RIPPLE:
                return new Ripple(this.fiatValue, this.store);
            case $CRYPTO.NANO:
                return new Nano(this.fiatValue, this.store);
            default:
                throw new Error('Invalid crypto type');
        }
    }

    destroyCryptoInstance(previousCrypto) {
        Object.keys(previousCrypto).forEach(key => delete previousCrypto[key]);
    }
}

export const walletMaker = () => {
    return {
        [$CRYPTO.BTC]: null,
        [$CRYPTO.DIVI]: null,
        [$CRYPTO.DOGE]: null,
        [$CRYPTO.ETH]: null,
        [$CRYPTO.NANO]: null,
        [$CRYPTO.RIPPLE]: null,
    }
}

export const cryptoMaker = (store) => {
    return {
        [$CRYPTO.BTC]: (amount) => new BTC(amount, store),
        [$CRYPTO.DIVI]: (amount) => new Divi(amount, store),
        [$CRYPTO.DOGE]: (amount) => new Doge(amount, store),
        [$CRYPTO.ETH]: (amount) => new Ethereum(amount, store),
        [$CRYPTO.NANO]: (amount) => new Nano(amount, store),
        [$CRYPTO.RIPPLE]: (amount) => new Ripple(amount, store),
    }
};

export class BTC extends Crypto {
    constructor(fiatVal, store) {
        super({
            name: $CRYPTO.BTC,
            store: store,
            fiatVal: fiatVal
        });
    }
}

