import { $i, $q, $a, $x, $CRYPTO, $GBP } from '../lib/constants.js';
import { Crypto, BTC, Divi, Ethereum, Doge, Ripple, Nano, cryptoMaker, walletMaker } from '../models/Crypto.js';
import utils from '../lib/utils.js';

export default class Trader {
    constructor({ 
        _id, index, guid, isActive, balance, age, 
        picture, firstname, lastname, company, email, 
        phone, registered, latitude, longitude, store }) {
        this._id = _id;
        this.store = store;
        this.index = index;
        this.guid = guid;
        this.isActive = isActive;
        this.balance = this.convertBalance(balance);
        this.age = age;
        this.picture = picture;
        this.firstname = firstname;
        this.lastname = lastname;
        this.company = company;
        this.email = email;
        this.phone = phone;
        this.registered = registered;
        this.latitude = Number.parseFloat(latitude);
        this.longitude = Number.parseFloat(longitude);
        this.wallet = walletMaker();
        this.cryptoMaker = cryptoMaker(this.store);
        
        this.convertBalanceToCryptos();
    }

    convertBalance(balance) {
        return Number.parseFloat(balance.substring(1, balance.length).replace(',', ''))
    }

    convertBalanceToCryptos() {
        const walletKeys = Object.keys(this.wallet);
        [...utils.splitNParts(100, 6)].forEach((percent, i) => {
            this.wallet[walletKeys[i]] = this.cryptoMaker[walletKeys[i]]((this.balance / percent) * 100)
        });
    }

    
}