import { keys, $x, $CRYPTO } from '../lib/constants.js';


export class Player {
    constructor(store) {
        let _stash = new Proxy({}, {
            set: (obj, prop, value) => {
                const oldValue = obj[prop];
                obj[prop] = value;
                store.events.publish(keys.$eventKey.STASH_CHANGED, { prop, val, oldValue });
            }
        });

        let _wallet = new Proxy({}, {
            set: (obj, prop, value) => {
                const oldValue = obj[prop];
                obj[prop] = value;
                store.events.publish(keys.$eventKey.WALLET_CHANGED, { prop, val, oldValue });
            }
        })

        let _trenchCoat = new Proxy([], {
            set: (obj, prop, val) => {
                const oldValue = obj[prop];
                obj[prop] = value;
                store.events.publish(keys.$eventKey.TRENCH_COAT_CHANGED, { prop, val, oldValue });
            }
        })

        this.stash = this.getStashMods(_stash);
        this.trenchCoat = this.getTrenchCoatMods(_trenchCoat);
        this.wallet = this.getWalletMods(_wallet);

        this.stash.set(2000, $x);
        this.trenchCoat.set(new Array(100), $x);
    }

    getStashMods(_stash) {
        return {
            inc: (val, k) => k == $x ? _stash += val : void 0,
            dec: (val, k) => k == $x ? _stash -= val : void 0,
            set: (val, k) => k == $x ? _stash = val : void 0,
            get: () => _stash
        }
    }

    getWalletMods(_wallet) {
        return {
            [$CRYPTO.BTC]: {
                inc: (val, k) => k == $x ? _wallet[$CRYPTO.BTC] += val : void 0,
                dec: (val, k) => k == $x ? _wallet[$CRYPTO.BTC] -= val : void 0,
                set: (val, k) => k == $x ? _wallet[$CRYPTO.BTC] = val : void 0,
                get: () => _wallet[$CRYPTO.BTC]
            },
            [$CRYPTO.DIVI]: {
                inc: (val, k) => k == $x ? _wallet[$CRYPTO.DIVI] += val : void 0,
                dec: (val, k) => k == $x ? _wallet[$CRYPTO.DIVI] -= val : void 0,
                set: (val, k) => k == $x ? _wallet[$CRYPTO.DIVI] = val : void 0,
                get: () => _wallet[$CRYPTO.DIVI]
            },
            [$CRYPTO.DOGE]: {
                inc: (val, k) => k == $x ? _wallet[$CRYPTO.DOGE] += val : void 0,
                dec: (val, k) => k == $x ? _wallet[$CRYPTO.DOGE] -= val : void 0,
                set: (val, k) => k == $x ? _wallet[$CRYPTO.DOGE] = val : void 0,
                get: () => _wallet[$CRYPTO.DOGE]
            },
            [$CRYPTO.ETH]: {
                inc: (val, k) => k == $x ? _wallet[$CRYPTO.ETH] += val : void 0,
                dec: (val, k) => k == $x ? _wallet[$CRYPTO.ETH] -= val : void 0,
                set: (val, k) => k == $x ? _wallet[$CRYPTO.ETH] = val : void 0,
                get: () => _wallet[$CRYPTO.ETH]
            },
            [$CRYPTO.NANO]: {
                inc: (val, k) => k == $x ? _wallet[$CRYPTO.NANO] += val : void 0,
                dec: (val, k) => k == $x ? _wallet[$CRYPTO.NANO] -= val : void 0,
                set: (val, k) => k == $x ? _wallet[$CRYPTO.NANO] = val : void 0,
                get: () => _wallet[$CRYPTO.NANO]
            },
            [$CRYPTO.RIPPLE]: {
                inc: (val, k) => k == $x ? _wallet[$CRYPTO.RIPPLE] += val : void 0,
                dec: (val, k) => k == $x ? _wallet[$CRYPTO.RIPPLE] -= val : void 0,
                set: (val, k) => k == $x ? _wallet[$CRYPTO.RIPPLE] = val : void 0,
                get: () => _wallet[$CRYPTO.RIPPLE]
            },
        }
    }

    getTrenchCoatMods(_trenchCoat) {
        return {
            add: (val, k) => k == $x ? _trenchCoat.push(val) : void 0,
            remove: (val, k) => k == $x ? _trenchCoat.splice(_trenchCoat.indexOf(val)) : void 0,
            set: (val, k) => k == $x ? _trenchCoat = val : void 0,
            getCoat: () => _trenchCoat
        }
    }
}

